import { ReportsService } from '../../../../../auth/_services/reports.service';
import { Component, OnInit, ViewEncapsulation, AfterViewInit } from '@angular/core';
import { Helpers } from '../../../../../helpers';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { DataService } from '../../../../../_services/data.service';
import { MenuService } from '../../../../../auth/_services/menu.service';
import { ProductService } from '../../../../../auth/_services/product.service';
import { CategoryService } from '../../../../../auth/_services/category.service';
import { SubCategoryService } from '../../../../../auth/_services/subCategory.service';
import { BrandService } from '../../../../../auth/_services/brand.service';
import { AmChartsService } from "@amcharts/amcharts3-angular";

import { Router, ActivatedRoute } from "@angular/router";
import { ToastrService } from 'ngx-toastr';


@Component({
    selector: 'app-overview',
    templateUrl: "overview.component.html",
    encapsulation: ViewEncapsulation.None,
    styles: []
})
export class OverviewComponent implements OnInit {
    private chart: any;

    constructor(private AmCharts: AmChartsService, private route: ActivatedRoute, private toastr: ToastrService, private router: Router, private _reportsService: ReportsService, private _brandService: BrandService, private _menuService: MenuService, private _categoryService: CategoryService, private _productService: ProductService, private _subCategory: SubCategoryService, private _dataService: DataService, private _script: ScriptLoaderService) { }
    public customerData;
    public productData;
    public searchValue = "";
    public dropDownValue = '';
    public dashboardData: any = {};
    public menu: Array<any> = [];
    public chart_data: Array<any> = [];
    public average_sale = 0;
    public from_top_selling = '';
    public to_top_selling = '';


    public currentUser;

    p: number = 1;

    ngOnInit() {

        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.report_overview != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/



        let user_id: number;
        let user_role: number;
        let loggedUserId: any;
        let where: any;
        this.route.params.subscribe(
            parameter => {
                localStorage.setItem('section', 'product');
                loggedUserId = JSON.parse(localStorage.getItem('currentUser'));
                if (parameter.vendor_id) {
                    console.log('parameter.vendor_id', parameter.vendor_id);
                    where = { loggedUserId: parameter.vendor_id, role: '' };
                } else {
                    where = { loggedUserId: loggedUserId.success.id, role: loggedUserId.success.role };
                }
                this._reportsService.reportInventory(where).subscribe(
                    data => {
                        this.productData = data.success;
                    }
                );
            }
        );

        this.currentUser = JSON.parse(localStorage.getItem('currentUser'));


        user_id = this.currentUser.success.id;
        user_role = this.currentUser.success.role;

        this._menuService.searching({ dropdown_value: 'Active', search_value: "" }).subscribe(
            data => {
                let menu1: any;
                let menuData: any;
                menuData = data.success;
                menuData.forEach(element => {
                    menu1 = {
                        item_id: element.id,
                        item_text: element.menu_name
                    }
                    this.menu.push(menu1);

                });
                localStorage.setItem('menu', JSON.stringify(this.menu));

            }
        );





        this._menuService.dashboardService({ user_role: user_role, user_id: user_id })
            .subscribe(
            data => {
                this.dashboardData.total_orders = data.total_orders[0].total_orders;
                this.dashboardData.total_sales = data.total_sales[0].total_amt;
                this.dashboardData.today_total_order = data.today_total_order[0].total_orders;
                this.dashboardData.today_total_sales = data.today_total_sales[0].total_amt;
                this.dashboardData.total_delivered_order = data.total_delivered_order[0].total_orders;
                this.dashboardData.total_products = data.total_products[0].total_products;
                this.dashboardData.total_instock_product = data.total_instock_product[0].total_products;
                this.dashboardData.total_outstock_product = data.total_outstock_product[0].total_products;
                this.dashboardData.total_pending_order = data.total_pending_order[0].total_orders;
                this.dashboardData.quater_sale1 = data.quater_sale[0].Q1;
                this.dashboardData.quater_sale2 = data.quater_sale[0].Q2;
                this.dashboardData.quater_sale3 = data.quater_sale[0].Q3;
                this.dashboardData.quater_sale4 = data.quater_sale[0].Q4;
                this.dashboardData.vendor_awaiting_approval = data.quater_sale[0].vendor_awaiting_approval;
                this.dashboardData.vendor_signup_this_month = data.quater_sale[0].vendor_signup_this_month;
                this.dashboardData.total_sales_chart = data.total_sales_chart;
                this.dashboardData.top_selling = data.top_selling;
                this.dashboardData.top_earning = data.top_earning;

                this.average_sale = data.total_sales_avg[0].total_amt.toFixed(2);

                console.log('this.average_sale');
                console.log(this.average_sale);
                console.log(this.dashboardData);
                //this.chart_data = 
                this.createChart('overview_monthly_sale', data.total_sales_chart);
                this.createChart('sales-by-day-chart', data.total_sales_chart);

            }
            );
    }



    public top_selling() {
        console.log('from_top_selling');
        console.log(this.from_top_selling);
        console.log('to_top_selling');
        console.log(this.to_top_selling);
        console.log('top_selling');
    }

    public search() {
        this._reportsService.searching({ search_value: this.searchValue, dropdown_value: this.dropDownValue }).subscribe(
            data => {
                console.log(data);
                this.customerData = data.success;
            }
        );
    }

    public customer_delete(del_id) {

        console.log(del_id);

        let value = confirm('Are you sure to delete this customer request?');
        if (value) {

            this._reportsService.del_support(del_id).subscribe(
                data => {
                    this.customerData = data.data;
                }
            );
        }

    }

    public createChart(div_id: string, data: any) {
        this.chart = this.AmCharts.makeChart(div_id, {
            type: "serial",
            theme: "light",
            marginRight: 40,
            marginLeft: 40,
            autoMarginOffset: 20,
            mouseWheelZoomEnabled: !0,
            dataDateFormat: "YYYY-MM-DD",
            valueAxes: [{
                id: "v1",
                axisAlpha: 0,
                position: "left",
                ignoreAxisWidth: !0
            }],
            balloon: {
                borderThickness: 1,
                shadowAlpha: 0
            },
            graphs: [{
                id: "g1",
                balloon: {
                    drop: !0,
                    adjustBorderColor: !1,
                    color: "#ffffff"
                },
                bullet: "round",
                bulletBorderAlpha: 1,
                bulletColor: "#FFFFFF",
                bulletSize: 5,
                hideBulletsCount: 50,
                lineThickness: 2,
                title: "red line",
                useLineColorForBulletBorder: !0,
                valueField: "value",
                balloonText: "<span style='font-size:18px;'>[[value]]</span>"
            }],
            chartScrollbar: {
                graph: "g1",
                oppositeAxis: !1,
                offset: 30,
                scrollbarHeight: 80,
                backgroundAlpha: 0,
                selectedBackgroundAlpha: .1,
                selectedBackgroundColor: "#888888",
                graphFillAlpha: 0,
                graphLineAlpha: .5,
                selectedGraphFillAlpha: 0,
                selectedGraphLineAlpha: 1,
                autoGridCount: !0,
                color: "#AAAAAA"
            },
            chartCursor: {
                pan: !0,
                valueLineEnabled: !0,
                valueLineBalloonEnabled: !0,
                cursorAlpha: 1,
                cursorColor: "#258cbb",
                limitToGraph: "g1",
                valueLineAlpha: .2,
                valueZoomable: !0
            },
            valueScrollbar: {
                oppositeAxis: !1,
                offset: 50,
                scrollbarHeight: 10
            },
            categoryField: "date",
            categoryAxis: {
                parseDates: !0,
                dashLength: 1,
                minorGridEnabled: !0
            },
            export: {
                enabled: !0
            },
            dataProvider: data
        });
    }


    public changeStatus(customer: any) {

        if (customer.customer_status == 'Active') {
            customer.customer_status = 'Inactive';

            this._reportsService.updateStatus(customer.id).subscribe(
                data => {
                    console.log('update', data);
                }
            );
        } else {
            customer.customer_status = 'Active';
            this._reportsService.updateStatus(customer.id).subscribe(
                data => {
                    console.log('update', data);
                }
            );
        }
    }

    ngAfterViewInit() {
        this._script.loadScripts('app-overview',
            ['assets/app/js/overview.js', '//www.amcharts.com/lib/3/plugins/tools/polarScatter/polarScatter.min.js',
                '//www.amcharts.com/lib/3/plugins/export/export.min.js',
                'assets/demo/default/custom/components/charts/amcharts/charts.js']);
        Helpers.loadStyles('app-overview', [
            '//www.amcharts.com/lib/3/plugins/export/export.css']);

    }


}
